@media screen and (max-width: 700px) {
    .list {
        display: block;
        margin-left: auto;
        margin-right: auto;
    }

    .item:not(:last-child) {
        margin-bottom: 20px;
    }
}

@media screen and (min-width:700px)and (max-width:1200px) {
    .item {

        flex-basis: calc((100% - 2 * 15px) / 2);
        /* margin-right: 15px; */

    }

    .item:not(:nth-last-child(-n + 2)) {
        margin-bottom: 20px;
    }

    /* .item:not(:nth-last-child(-2n)) {
    margin-right: 10px; */
}
@media screen and (min-width: 1200px) {
    .item {
        flex-basis: calc((100% - 3 * 15px) / 4);
    }
    .item:not(:nth-last-child(-n+4)) {
        margin-bottom: 20px;
    }

}
