    /* пагинация */
        .navigationButtons {
            /* width: 60%; */
            height: 40px;
            list-style: none;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            margin: auto;
            padding-top: 40px;
        }
    
        .navigationButtons a {
            padding: 10px;
            margin: 4px;
            border-radius: 5px;
            border: 1px solid rgb(100, 22, 136);
            color: rgb(125, 3, 186);
            cursor: pointer;
        }
    
        .navigationButtons a:hover {
            color: white;
            background-color: rgb(200, 0, 255);
        }
    
        .navigationActive a {
            color: white;
            background-color: rgb(96, 69, 93);
        }
    
        .navigationDisabled a {
            color: rgb(128, 128, 128);
            background-color: rgb(172, 20, 149);
        }